html,
body,
#root {
  height: 100%;
  font-size: 15px;
}

:root {
  --primary-dark: #00599c; /*user name*/
  --primary: #1b74b6;
  --primary-medium: #2a83be;
  --primary-light: #97c0de; /*avatar border*/
  /* --primary-lighter: #a1c1e6; */
  --primary-background: #cadeee; /*blue bubble*/
  --ui-dark-01: #2c3748; /*sidebar*/
  --ui-dark-02: #323d4b; /*hover background*/
  --ui-dark-03: #31475e; /*active highlight*/
  --ui-dark-04: #878ea1; /*text*/
  --ui-dark-05: #e0e0e8; /*highlight text*/
  --ui-01: #f2f2f2; /*chat background*/
  --black: 38, 37, 36; /* chat text */
  --gray: 86, 88, 103; /* message input */
  --gray-light: #c8c9cf; /*more menu border */
  --font-regular: "Avenir-Next", sans-serif;
}

body,
input {
  font-family: "Avenir-Next", sans-serif;
  font-family: var(--font-regular);
}

body {
  margin: 0;
  padding: 0;
  font-weight: 400;
}

button {
  margin: 0;
  padding: 0;
  background: none;
  border: none;
  font-family: "Avenir-Next", sans-serif;
  font-family: var(--font-regular);
}

input {
  margin: 0;
}

@font-face {
  font-family: "Avenir-Next";
  font-weight: normal;
  font-style: normal;
  src: url(/chatbox/static/media/avenir-next-regular.457e94a5.woff2) format("woff2"),
    url(/chatbox/static/media/avenir-next-regular.a17c827d.woff) format("woff");
}

@font-face {
  font-family: "Avenir-Next";
  font-weight: bold;
  font-style: normal;
  src: url(/chatbox/static/media/avenir-next-demi.d42bfc91.woff2) format("woff2"),
    url(/chatbox/static/media/avenir-next-demi.7d6afb9b.woff) format("woff");
}

.User {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.1s ease;
  color: var(--ui-dark-04);
  border-bottom: 1px solid var(--ui-dark-03);
}

.User:last-of-type {
  border-bottom: none;
}

.User:not(.active):hover {
  background: var(--ui-dark-03);
}

.User.visible .User__details {
  display: block;
}

.User.active {
  background: var(--ui-dark-03);
  box-shadow: inset 5px 0px 0px var(--primary-light);
  color: var(--ui-dark-05);
  position: relative;
}

.User.active:before,
.User.active:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: #34354a;
  margin-left: -15px;
}

.User.active:before {
  top: -1px;
}

.User.active:after {
  bottom: -1px;
}

.User:focus {
  outline: none;
  background: var(--ui-dark-03);
}

.User__pic {
  width: 50px;
  border-radius: 50%;
  transition: width 0.3s ease;
}

.User__details-name {
  margin: 0;
  font-size: 1rem;
  margin-bottom: 0.4rem;
  text-align: left;
  font-weight: 700;
  font-size: 0.95rem;
}

.User__details {
  display: block;
  padding: 0 0 0 1rem;
}

.User__details-name {
  margin: 0;
  font-size: 1rem;
  margin-bottom: 0.3rem;
  text-align: left;
  font-weight: 700;
  font-size: 0.95rem;
}

.User__details-status {
  margin: 0;
  padding: 0;
  text-align: left;
  font-size: 0.9rem;
}

@media (max-width: 749px) {
  .User__details {
    display: none;
  }
}

@media (max-width: 550px) {
  .User {
    padding: 0.75rem;
  }

  .User.hidden .User__pic {
    width: 37.5px;
  }
}

.Sidebar {
